import React from 'react';
import { node, string } from 'prop-types';
import classnames from 'classnames';

const CSS_PREFIX = 'vds-select-group-delimiter';

const SelectGroupDelimiter = ({ children, className, ...remainingProps }) => (
  <span
    aria-hidden="true"
    className={classnames(CSS_PREFIX, className)}
    {...remainingProps}
  >
    {children}
  </span>
);

SelectGroupDelimiter.propTypes = {
  /**
  * @ignore
  */
  children: node,

  /**
   * @ignore
   */
  className: string
};

SelectGroupDelimiter.displayName = 'SelectGroupDelimiter';

export default SelectGroupDelimiter;
